.contact-grid {
  display: flex;
  flex-wrap: wrap;
  margin-left: -3rem;
}

.contact-grid > * {
  padding-left: 3rem;
  width: 100%;
}

@media (min-width: 56em) {
  .contact-grid > *:first-child {
    width: 33.3333333333%;
  }

  .contact-grid > *:last-child {
    width: 66.6666666667%;
  }
}

.contact-page .hero {
  margin-bottom: 0;
}

